.sidebar {
  width: 15%;
  background: #e6e6e6;
  min-height: calc(100vh - 47px);
}

.sidebar_menu{
  padding-top: 10px;
}

.sidebar_menu a{
  display: block;
  width: calc(100% - 47px);
  color: black;
  text-decoration: none;
  padding: 13px 21px 13px 26px;

  font-size: 16px;


}




